<template>
    <div>
        <el-dialog title="编辑" :visible.sync="dialogppmmisible" @close="closeDialog" width="30%">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="系统权限">
                    <el-checkbox-group v-model="form.system">
                        <el-checkbox label="管理员管理" name="type"></el-checkbox>
                        <el-checkbox label="角色管理" name="type"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="部门权限">
                    <el-checkbox-group v-model="form.department">
                        <el-checkbox label="外交部" name="type"></el-checkbox>
                        <el-checkbox label="国防部" name="type"></el-checkbox>
                        <el-checkbox label="教育部" name="type"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                 <el-form-item label="状态">
                    <el-switch v-model="info.state"></el-switch>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">修改</el-button>
                    <el-button @click="dialogppmmisible=false">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            info: {},
            dialogppmmisible: false,
            form: {
                system: [],
                department: [],
                state:""
            }
        };
    },
    props: {
        ppmmVisible: Boolean,
        ppmmitem: Object
    },
    watch: {
        ppmmVisible(newValue, oldValue) {
            this.dialogppmmisible = newValue;
        },
        ppmmitem(newValue, oldValue) {
            this.info = newValue;
        }
    },
    methods: {
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        onSubmit() {
            this.$message({
                message: "修改成功",
                type: "success"
            });
            this.dialogppmmisible = false;
        }
    }
};
</script>


