<template>
    <div>
        <el-main>
            <div class="select">
                <el-input v-model="id" placeholder="ID" clearable></el-input>
                <el-input v-model="name" placeholder="角色名" clearable></el-input>
                <el-select
                    v-model="power"
                    multiple
                    collapse-tags
                    placeholder="请选择权限"
                >
                    <el-option v-for="item in powers" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-button type="primary" icon="el-icon-search" class="search"></el-button>
            </div>
            <div class="btn">
                <el-button type="danger" icon="el-icon-delete-solid">批量删除</el-button>
                <el-button type="primary" icon="el-icon-document" @click="exportExcel()">导出</el-button>
            </div>
            <el-table
                id="out-table"
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'id'}"
                :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="id" label="ID" sortable></el-table-column>
                <el-table-column prop="name" label="角色名"></el-table-column>
                <el-table-column prop="power" label="拥有权限"></el-table-column>
                <el-table-column prop="describe" label="描述"></el-table-column>
                <el-table-column
                    prop="state"
                    label="状态"
                    min-width="100"
                    :filters="[{ text: '已启用', value: true }, { text: '已停用', value: false }]"
                    :filter-method="filterState"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.state === true ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.state==true?'已启用':'已停用'}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                layout="prev, pager, next"
                :total="1000"
            ></el-pagination>
        </el-main>
        <ppmm :ppmmitem="ppmmitem" :ppmmVisible="ppmmVisible" @close-dialogStatus="Close_dialog"></ppmm>
    </div>
</template>

<script>
import ppmm from "@/components/dialog/ppmm";
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
    data() {
        return {
            ppmmitem: {},
            ppmmVisible: false,
            ip: "",
            time: "",
            name: "",
            roles: "",
            tableData: [
                {
                    id: "1",
                    name: "超级管理员",
                    power: ["all"],
                    describe: "拥有最高权限",
                    state: true
                },
                {
                    id: "2",
                    name: "国防部管理员",
                    power: ["国防部"],
                    describe: "",
                    state: true
                },
                {
                    id: "3",
                    name: "教育部管理员",
                    power: ["教育部"],
                    describe: "",
                    state: true
                },
                {
                    id: "4",
                    name: "高级管理员",
                    power: ["外交部，国防部"],
                    describe: "",
                    state: true
                },
                {
                    id: "5",
                    name: "外交部管理员",
                    power: ["外交部"],
                    describe: "",
                    state: true
                }
            ],
            powers: [
                {
                    value: "管理员管理"
                },
                {
                    value: "角色管理"
                },
                {
                    value: "外交部"
                },
                {
                    value: "国防部"
                },
                {
                    value: "教育部"
                }
            ],
            power: []
        };
    },
    components: {
        ppmm
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        Close_dialog(val) {
            this.ppmmVisible = false;
        },
        handleEdit(index, row) {
            this.ppmmitem = row;
            this.ppmmVisible = true;
        },
        handleDelete(index, row) {
            this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$message({
                    type: "success",
                    message: "删除成功!"
                });
                for (let i in this.tableData) {
                    if (this.tableData[i] == row) {
                        this.tableData.splice(i, 1);
                    }
                }
            });
        },
        exportExcel() {
            var wb = XLSX.utils.table_to_book(
                document.querySelector("#out-table")
            );
            var wbout = XLSX.write(wb, {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            });
            try {
                FileSaver.saveAs(
                    new Blob([wbout], { type: "application/octet-stream" }),
                    "role.xlsx"
                );
            } catch (e) {
                if (typeof console !== "undefined") console.log(e, wbout);
            }
            return wbout;
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.select, .el-input {
    width: 156px;
}

.search {
    position: relative;
    top: -1px;
}
.el-select .el-select--small {
    margin-left 0
}
</style>


